.strikeThrough{
    text-decoration: line-through;
    opacity: 0.5;
}
.display-inline{
    display: inline;
}
.zeroState{
    color:#808080;
    /* position: absolute !important; */
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 10px;
}
.zeroStateText{
    font-size: x-large;
    line-height: 1.8;
    text-align: center;
    display: flex;
    justify-content: center;
}
.icon-button{
    background-color: transparent;
    border: none;
    color: white;
    padding: 12px 16px;
    font-size: 16px;
    cursor: pointer;
    font-size: 20px;
}